/* App.css – PresalesOverview Light Theme Shell (Global)
   Goals:
   - system UI font
   - 12–14px readable enterprise sizing
   - warm light background w/ subtle radial gradients
   - white rounded panels (14–16px radius) + thin slate borders + soft shadow
   - deep blue primary actions, white secondary actions
   - minimalist tables/inputs (12px headers)
   - modals = white card + soft overlay
   - all text dark/black for clarity
*/

:root {
  /* Base surfaces */
  --bg-0: #f7f8fb;
  --bg-1: #ffffff;

  /* Text */
  --ink-0: #0b1220;     /* main dark text */
  --ink-1: #111827;     /* slightly softer */
  --muted: #475569;     /* slate */
  --muted-2: #64748b;

  /* Borders & shadow */
  --border: rgba(148, 163, 184, 0.55);
  --border-2: rgba(148, 163, 184, 0.35);
  --shadow: 0 10px 28px rgba(15, 23, 42, 0.08);
  --shadow-2: 0 18px 40px rgba(15, 23, 42, 0.10);

  /* Radius */
  --radius: 16px;
  --radius-sm: 12px;

  /* Primary */
  --primary: #1d4ed8;     /* deep blue */
  --primary-2: #1e40af;
  --primary-soft: rgba(29, 78, 216, 0.12);
  --focus: rgba(29, 78, 216, 0.22);

  /* Status */
  --success: #16a34a;
  --success-soft: rgba(22, 163, 74, 0.12);
  --warning: #d97706;
  --warning-soft: rgba(217, 119, 6, 0.14);
  --danger: #dc2626;
  --danger-soft: rgba(220, 38, 38, 0.12);

  /* Typography */
  --font: system-ui, -apple-system, BlinkMacSystemFont, "SF Pro Text",
    "Segoe UI", Roboto, sans-serif;

  /* Sizing */
  --fz-body: 14px;     /* was 13px */
  --fz-title: 24px;    /* page title target */
  --fz-h3: 14px;
  --fz-small: 12px;    /* headers/labels */
}

/* Reset */
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
  font-family: var(--font);
  font-size: var(--fz-body);
  line-height: 1.5;
  color: var(--ink-0);

  /* Warm light background + subtle radial gradients */
  background:
    radial-gradient(circle at 12% 6%, rgba(29, 78, 216, 0.10), transparent 46%),
    radial-gradient(circle at 85% 14%, rgba(15, 118, 110, 0.08), transparent 48%),
    radial-gradient(circle at 55% 90%, rgba(234, 179, 8, 0.06), transparent 55%),
    var(--bg-0);

  background-attachment: fixed;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Basic elements */
h1, h2, h3, h4, h5, h6 {
  color: var(--ink-0);
  margin: 0;
  letter-spacing: -0.02em;
}

p {
  margin: 0;
  color: var(--muted);
}

a {
  color: inherit;
}

/* ========= App Shell ========= */

.app-container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.app-header {
  position: sticky;
  top: 0;
  z-index: 1000;

  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  gap: 16px;

  padding: 18px 20px;
  border-bottom: 1px solid var(--border);

  background: rgba(248, 250, 252, 0.96);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);

  box-shadow: 0 1px 0 rgba(15, 23, 42, 0.06),
              0 8px 24px rgba(15, 23, 42, 0.04);
}

.app-header h1 {
  font-size: 26px;
  font-weight: 800;
  color: var(--ink-0);
}

.app-header-main p {
  margin: 6px 0 0;
  font-size: 12px;
  color: var(--muted);
}

/* Nav */
.app-nav {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
  justify-content: flex-end;
}

.nav-link {
  padding: 8px 12px;
  border-radius: 999px;
  font-size: 12px;
  font-weight: 700;
  border: 1px solid transparent;
  color: var(--ink-0);
  text-decoration: none;
  background: transparent;
  transition: background 0.18s ease, border-color 0.18s ease, transform 0.08s ease;
}

.nav-link:hover {
  border-color: rgba(29, 78, 216, 0.35);
  background: rgba(29, 78, 216, 0.06);
  transform: translateY(-1px);
}

.nav-link.active {
  border-color: rgba(29, 78, 216, 0.45);
  background: rgba(29, 78, 216, 0.12);
}

/* Main */
.app-main {
  flex: 1;
  padding: 16px 20px 24px;
}

/* ========= Home Layout ========= */

.home-dashboard {
  display: grid;
  grid-template-columns: 1fr;
  gap: 16px;
  align-items: flex-start;
}

.home-top-row {
  grid-column: 1 / -1;
}

/* Single definition only (you had this twice) */
.home-main-column {
  max-width: 100%;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.home-side-column {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

/* ========= Home Cards ========= */

.home-card {
  background: rgba(255, 255, 255, 0.94);
  border-radius: var(--radius);
  border: 1px solid var(--border);
  padding: 12px 14px;
  box-shadow: var(--shadow);
}

.home-card-wide {
  width: 100%;
}

.home-card-title {
  margin: 0 0 2px;
  font-size: var(--fz-h3);
  font-weight: 750;
  color: var(--ink-0);
}

/* Make Top Deals title match Customer Portfolio */
.home-topdeals-title {
  font-size: var(--fz-title); /* 24px */
  font-weight: 800;
  margin-bottom: 4px;
}
.home-card-subtitle {
  margin: 0 0 10px;
  font-size: 12px;
  color: var(--muted);
}

/* Utility muted text */
.small-muted {
  font-size: 12px;
  color: var(--muted-2);
}

/* ========= KPI Strip ========= */


.home-kpi-strip {
  width: 100%;
  max-width: 1220px;          /* ✅ centers the whole strip */
  margin: 0 auto 12px;

  background: rgba(255, 255, 255, 0.86);
  border: 1px solid var(--border);
  border-radius: var(--radius);
  box-shadow: var(--shadow);
  padding: 12px 12px;

  display: grid;
  grid-template-columns: repeat(4, minmax(0, 1fr)); /* ✅ 4 cards per row */
  gap: 12px;
}


/* Section label inside KPI strip */
.home-kpi-section-label {
  grid-column: 1 / -1;
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 4px 2px 2px;
  font-size: 12px;
  font-weight: 850;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  color: var(--muted);
}

.home-kpi-section-label::after {
  content: "";
  height: 1px;
  flex: 1;
  background: linear-gradient(
    to right,
    rgba(148, 163, 184, 0.55),
    rgba(148, 163, 184, 0.10)
  );
}


.home-kpi-card {
  min-height: 104px;

  position: relative;
  overflow: hidden;

  background: linear-gradient(180deg, rgba(255, 255, 255, 0.96), rgba(255, 255, 255, 0.90));
  border-radius: 14px;
  border: 1px solid var(--border);
  padding: 12px 12px;
  box-shadow: 0 8px 20px rgba(15, 23, 42, 0.06);

  display: flex;
  flex-direction: column;
  justify-content: space-between;

  transition: transform 0.12s ease, box-shadow 0.18s ease, border-color 0.18s ease;
}

.home-kpi-card::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  height: 3px;
  width: 100%;
  background: rgba(29, 78, 216, 0.35);
}

.home-kpi-card::after {
  content: "";
  position: absolute;
  inset: -40px -60px auto auto;
  width: 160px;
  height: 160px;
  border-radius: 999px;
  background: radial-gradient(circle at 30% 30%, rgba(29, 78, 216, 0.14), transparent 62%);
  pointer-events: none;
}

.home-kpi-card:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-2);
  border-color: rgba(29, 78, 216, 0.35);
}

/* Tone accents */
.home-kpi-card[data-tone="blue"]::before {
  background: rgba(29, 78, 216, 0.45);
}
.home-kpi-card[data-tone="teal"]::before {
  background: rgba(15, 118, 110, 0.42);
}
.home-kpi-card[data-tone="amber"]::before {
  background: rgba(217, 119, 6, 0.45);
}
.home-kpi-card[data-tone="indigo"]::before {
  background: rgba(79, 70, 229, 0.42);
}
.home-kpi-card[data-tone="slate"]::before {
  background: rgba(71, 85, 105, 0.38);
}

.home-kpi-top {
  display: flex;
  align-items: center;
  gap: 10px;
}

.home-kpi-icon {
  width: 34px;
  height: 34px;
  border-radius: 12px;
  display: grid;
  place-items: center;
  font-size: 12px;
  font-weight: 850;
  color: var(--ink-0);
  background: rgba(29, 78, 216, 0.10);
  border: 1px solid rgba(29, 78, 216, 0.18);
}

.home-kpi-card[data-tone="teal"] .home-kpi-icon {
  background: rgba(15, 118, 110, 0.10);
  border-color: rgba(15, 118, 110, 0.18);
}
.home-kpi-card[data-tone="amber"] .home-kpi-icon {
  background: rgba(217, 119, 6, 0.12);
  border-color: rgba(217, 119, 6, 0.18);
}
.home-kpi-card[data-tone="indigo"] .home-kpi-icon {
  background: rgba(79, 70, 229, 0.10);
  border-color: rgba(79, 70, 229, 0.18);
}
.home-kpi-card[data-tone="slate"] .home-kpi-icon {
  background: rgba(71, 85, 105, 0.10);
  border-color: rgba(71, 85, 105, 0.18);
}

.home-kpi-top-text {
  min-width: 0;
}

.home-kpi-label {
  font-size: 12px;
  font-weight: 800;
  color: var(--muted);
  text-transform: uppercase;
  letter-spacing: 0.06em;
  margin-bottom: 2px;
}

.home-kpi-value {
  margin-top: 10px;
  font-size: 24px;
  font-weight: 900;
  color: var(--ink-0);
  line-height: 1.05;
}

.home-kpi-value-small {
  font-size: 13px;
  font-weight: 700;
  color: var(--ink-0);
}

.home-kpi-sub {
  margin-top: 4px;
  font-size: 12px;
  color: var(--muted-2);
}

/* ========= Top Deals Table (Home) ========= */

/* keep it as-is. wide table may still need horizontal scroll on smaller screens */
.home-topdeals-wrap {
  width: 100%;
  overflow-x: auto;
}

.home-topdeals-table,
.home-topdeals-table-wide {
  width: 100%;
  border-collapse: collapse;
  table-layout: fixed;
  font-size: 14px;
  color: var(--ink-0);
}

.home-topdeals-table th,
.home-topdeals-table td,
.home-topdeals-table-wide th,
.home-topdeals-table-wide td {
  padding: 8px 8px;
  border-bottom: 1px solid var(--border-2);
  vertical-align: top;
}

.home-topdeals-table th,
.home-topdeals-table-wide th {
  text-align: left;
  font-size: 12px;
  font-weight: 750;
  color: var(--muted);
  text-transform: uppercase;
  letter-spacing: 0.06em;
  background: transparent;
}

.th-right,
.td-right {
  text-align: right;
}

.td-ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.td-nowrap {
  white-space: nowrap;
}

/* Top Deals links */
.home-link {
  color: inherit;
  text-decoration: underline;
  font-weight: 700;
}
.home-link:hover {
  opacity: 0.85;
}

/* Top Deals – make customer stand out + nicer hover */
.home-topdeals-table td:first-child a,
.home-topdeals-table-wide td:first-child a {
  font-weight: 800;
}

.home-topdeals-table tbody tr:hover,
.home-topdeals-table-wide tbody tr:hover {
  background: rgba(29, 78, 216, 0.06);
}

/* ========= Notes ========= */

.home-card-header-row {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 12px;
}

.home-notes-actions {
  display: flex;
  gap: 8px;
  align-items: center;
}

/* Buttons (primary + secondary) */
.home-notes-btn {
  padding: 7px 12px;
  border-radius: 999px;
  border: 1px solid transparent;
  background: var(--primary);
  color: #ffffff;
  font-size: 12px;
  font-weight: 750;
  cursor: pointer;
  transition: background 0.18s ease, box-shadow 0.18s ease, transform 0.08s ease;
  box-shadow: 0 10px 18px rgba(29, 78, 216, 0.18);
}

.home-notes-btn:hover {
  background: var(--primary-2);
  box-shadow: 0 14px 26px rgba(29, 78, 216, 0.22);
  transform: translateY(-1px);
}

.home-notes-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

.home-notes-btn.secondary {
  border: 1px solid var(--border);
  background: #ffffff;
  color: var(--ink-0);
  box-shadow: 0 8px 16px rgba(15, 23, 42, 0.06);
}

.home-notes-btn.secondary:hover {
  background: rgba(148, 163, 184, 0.12);
  transform: translateY(-1px);
}

/* Notes view */
.home-notes-view {
  margin-top: 6px;
  border-radius: var(--radius-sm);
  border: 1px solid var(--border);
  background: #ffffff;
  padding: 10px 10px;
}

.home-notes-pre {
  margin: 0;
  white-space: pre-wrap;
  word-break: break-word;
  font-family: inherit;
  font-size: 14px;
  line-height: 1.55;
  color: var(--ink-0);
}

/* Notes edit */
.home-notes-textarea {
  width: 100%;
  min-height: 130px;
  margin-top: 6px;
  border-radius: var(--radius-sm);
  border: 1px solid var(--border);
  background: #ffffff;
  color: var(--ink-0);
  font-size: 14px;
  padding: 10px 10px;
  resize: vertical;
  outline: none;
  transition: border-color 0.18s ease, box-shadow 0.18s ease;
}

.home-notes-textarea::placeholder {
  color: var(--muted-2);
}

.home-notes-textarea:focus {
  border-color: rgba(29, 78, 216, 0.55);
  box-shadow: 0 0 0 4px var(--focus);
}

.home-notes-hint {
  margin-top: 6px;
  font-size: 12px;
  color: var(--muted-2);
}

/* ========= Generic States used by pages ========= */

.loading-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 60vh;
}

.loading-spinner {
  width: 36px;
  height: 36px;
  border: 3px solid rgba(148, 163, 184, 0.55);
  border-top-color: var(--primary);
  border-radius: 50%;
  animation: app-spin 1s linear infinite;
  margin-bottom: 10px;
}

.loading-text {
  font-size: 14px;
  color: var(--muted);
}

@keyframes app-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

/* ========= Modals (global helpers you can reuse) ========= */

.modal-backdrop {
  position: fixed;
  inset: 0;
  background: rgba(15, 23, 42, 0.35);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 16px;
  z-index: 60;
}

.modal-card {
  width: 100%;
  max-width: 720px;
  border-radius: var(--radius);
  background: #ffffff;
  border: 1px solid var(--border);
}

@media (max-width: 1200px) {
  .home-kpi-strip {
    grid-template-columns: repeat(4, minmax(0, 1fr));
  }
}

/* Responsive */
@media (max-width: 980px) {
  .home-kpi-strip {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}

@media (max-width: 520px) {
  .home-kpi-strip {
    grid-template-columns: 1fr;
  }
}
/* Reusable subtle table link (same as customer table) */
.table-link-btn {
  appearance: none;
  border: none;
  background: transparent;
  padding: 0;
  font: inherit;
  font-weight: 850;
  color: var(--ink-0);
  cursor: pointer;
  text-align: left;
  text-decoration: none;
}

.table-link-btn:hover {
  text-decoration: underline;
  text-underline-offset: 2px;
}

/* When row is hovered, link becomes primary */
.home-topdeals-table tbody tr:hover .table-link-btn {
  color: var(--primary-2);
}
